.fund {
    .nav {
        padding-top: 77px;
        position: relative;
        .login-box {
            position: absolute;
            right: 200px;
            top: 150px;
            width: 300px;
            color: #fff;
            background-color: #000;
            padding: 15px 10px;
            border-radius: 5px;
            .title {
                text-align: center;
                font-size: 18px;
                margin-bottom: 15px;
            }
            .form-input {
                background-color: transparent;
                border: 1px solid #fff;
                border-radius: 6px;
                display: block;
                width: 100%;
                margin-bottom: 15px;
                font-size: 14px;
                padding: 6px 8px;
                color: #fff;
            }
            .ercode {
                .form-input {
                    margin-bottom: 0;
                }
                .code {
                    background-color: #fff;
                    padding: 4px 10px;
                    margin-left: 10px;
                    color: #000;
                }
            }
            .login-btn {
                width: 100%;
                background-color: #FF5051;
                border: none;
                outline: none;
                text-align: center;
                color: #fff;
                margin-top: 20px;
                height: 32px;
                line-height: 32px;
                padding: 0;
                &:focus {
                    outline: none;
                }
            }
        }
    }
    .content {
        
        width: 1200px;
        margin: 0 auto;
        .top {
            padding: 50px 0;
            .title {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 50px;
            }
            .box {
                margin-left: -20px;
                margin-right: -20px;
                .n-item {
                    border: 1px solid #eaeaea;
                    border-radius: 10px;
                    margin: 0 20px;
                    min-height: 450px;
                    .ni-img {
                        width: 100%;
                        margin-bottom: 20px;
                    }
                    .ni-title {
                        font-size: 18px;
                        line-height: 2;
                        padding: 0 30px;
                        font-weight: bold;
                    }
                    .ni-subtit {
                        font-size: 16px;
                        line-height: 1.8;
                        padding:  0 30px;
                        color: #666;
                    }
                }
            }
        }
        .service {
            margin-top: 50px;
            .title {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 15px;
            }
            .sub-title {
                font-size: 14px;
                color: #666;
            }
            .s-row {
                margin-top: 30px;
                text-align: center;
                .s-col {
                    img {
                        width: 200px;
                    }
                    div {
                        text-align: center;
                        font-size: 18px;
                        color: #333;
                        font-weight: bold;
                    }
                }
            }
            .desc {
                margin-top: 100px;
                .d-item {
                    margin-right: 50px;
                }
                .title {
                    font-size: 18px;
                    font-weight: bold;
                }
                .sub-title {
                    font-size: 15px;
                    color: #666;
                    margin-bottom: 15px;
                }
                .point {
                    font-size: 14px;
                    line-height: 2;
                    position: relative;
                    padding-left: 20px;
                    color: #3370ff;
                    &::before {
                        position: absolute;
                        content: '';
                        width: 10px;
                        height: 10px;
                        border-radius: 50%;
                        background-color: #3370ff;
                        left: 0;
                        top: 10px;
                    }
                }
            }
        }
        .taurus {
            margin-top: 100px;
            padding-bottom: 50px;
            .title {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 20px;
                text-align: center;
            }
            .sub-title {
                text-align: center;
                font-size: 18px;
                color: #333;
                line-height: 1.8;
            }
            .desc {
                margin-top: 30px;
                font-size: 16px;
                color: #666;
                line-height: 2;
            }
        }
    }
    
}